import { Fragment, useState } from 'react'
import { useForm } from 'react-hook-form'
import Icon from 'components/Icon'
import theme from 'theme'

const Contact = ({ id, button = '', placeholder = 'Un message  nous adresser ?', success = '', inputs = [], method = 'message', toggle, ...props }) => {
  const [toggled, setToggled] = useState(false)
  const { register, watch, handleSubmit, formState: { isSubmitting, isSubmitSuccessful } } = useForm({ mode: 'onChange' })
  const onSubmit = async data => {
    fetch(`https://submit-form.com/${id}`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json',
      },
      body: JSON.stringify(data),
    })
    .then((res) => res.json().then(obj => console.log(obj)))
    .catch((error) => console.warn(error))
  }

  const values = watch()

  return (
    <Fragment>
      {toggle && (
        <button onClick={() => setToggled(!toggled)} className={`contact-form ${toggled ? 'active' : ''}`}>
          <Icon children={{ buy: 'palette', message: 'informations', newsletter: 'send' }[method]} style={{ margin: '0 0.5rem 0 0' }} />
          {button || {
            buy: `Acqurir cette oeuvre`,
            message: `Poser une question`,
            newsletter: `S'inscrire  la newsletter`,
          }[method]}
        </button>
      )}
      {(!toggle || toggled) && (
        <form css={Contact.styles.element} onSubmit={handleSubmit(onSubmit)}>
          {inputs.map(({ name, type = 'hidden', ...input }) => (
            <input {...input} ref={register} type={type} name={name} key={name} />
          ))}
          {/* honeypot / spam filtering */}
          <input ref={register()} type='text' name='address' style={{ display: 'none' }} tabIndex='-1' autoComplete='off' />
          {['buy', 'message'].includes(method) ? (
            <Fragment>
              <textarea ref={register()} name='message' maxLength='1000' placeholder={placeholder} />
              <div>
                <input ref={register({ required: true })} name='name' type='text' placeholder='Prnom, Nom' required={true} />
              </div>
              <small>Indiquez votre numro de tlphone ou votre adresse email afin d'tre recontacts :</small>
              <div>
                <input ref={register({ required: !values.phone })} name='email' type='email' placeholder='Email' required={!values.phone} />
                <input ref={register({ required: !values.email })} name='phone' type='tel' placeholder='Tlphone' required={!values.email} />
              </div>
              <button className={`contact-form ${isSubmitSuccessful ? 'active' : ''}`} type='submit' disabled={isSubmitSuccessful || isSubmitting}>
                <Icon children={isSubmitSuccessful ? 'check' : isSubmitting ? 'loading' : 'send'} style={{ margin: '0 0.5rem 0 0' }} />
                {isSubmitSuccessful ? 'Merci,  bientt !' : isSubmitting ? '...' : 'Envoyer'}
              </button>
            </Fragment>
          ) : method === 'newsletter' ? (
            <Fragment>
              <div>
                <input ref={register({ required: true })} name='email' type='email' placeholder='Email' required={true} />
                <input ref={register({ required: true })} name='name' type='text' placeholder='Prnom, Nom' required={true} />
                <input ref={register()} name='city' type='text' placeholder='Ville' />
              </div>
              <button className={`contact-form ${isSubmitSuccessful ? 'active' : ''}`} type='submit' disabled={isSubmitSuccessful || isSubmitting}>
                <Icon children={isSubmitSuccessful ? 'check' : isSubmitting ? 'loading' : 'send'} style={{ margin: '0 0.5rem 0 0' }} />
                {isSubmitSuccessful ? 'Merci,  bientt !' : isSubmitting ? '...' : "S'inscrire"}
              </button>
            </Fragment>
          ) : null}
          {isSubmitSuccessful && success && (
            <em><small>{success}</small></em>
          )}
          <details className="rgpd">
            <summary>RGPD - Protection de vos donnes</summary>
            Les informations recueillies via le site www.galeriegaia.fr (ci-aprs dsign  le Site ) ont vocation  tre traites par la Galerie Gaa, responsable de traitement, aux fins de traitement de votre demande de renseignement. Les informations ci dessus sont obligatoires pour la gestion de vos demandes. Conformment  la rglementation applicable en matire de protection des donnes  caractre personnel, vous disposez dun droit daccs de rectification et de portabilit des informations vous concernant; d'un droit de limitation, deffacement et dopposition pour des motifs lgitimes au traitement de vos donnes; de la possibilit de nous transmettre des directives afin dorganiser le sort des donnes vous concernant (conservation, effacement, communication  un tiers, etc.) en cas de dcs; Vous pouvez exercer ces droits en crivant  l'adresse lectronique suivante : galeriegaia@orange.fr. Toutefois, votre opposition peut, en pratique et selon le cas, avoir une incidence sur votre demande dinformation. Pour plus dinformations concernant ce traitement contactez-nous  l'adresse galeriegaia@orange.fr.
          </details>
        </form>
      )}
    </Fragment>
  )
}

Contact.styles = {
  element: {
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
    '>*': {
      margin: '0.5rem 0',
    },
    '>div': {
      display: 'flex',
      [theme.medias.small]: {
        flexDirection: 'column',
      },
      '>input': {
        width: '100%',
        margin: '0 1rem',
        [theme.medias.small]: {
          margin: '0.5rem 0',
        },
        ':first-of-type': {
          marginTop: 0,
          marginLeft: 0,
        },
        ':last-of-type': {
          marginBottom: 0,
          marginRight: 0,
        },
      },
    },
    '>small': {
      padding: '0.5rem 0.5rem 0',
      fontStyle: 'italic',
    },
    '>textarea': {
      width: '100%',
      lineHeight: 1.5,
    },
    '>button': {
      width: '100%',
    },
  },
}

export default Contact
